.page {
  max-width: 1280px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 45px 0 60px;
  font-family: 'Inter', 'Arial', sans-serif;
  background: #000000;
  box-sizing: border-box;
}

@media screen and (max-width: 530px) {
  .page {
    padding-bottom: 36px;
    padding-top: 28px;
  }
}